@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Semi+Condensed:wght@600&display=swap);
html, body {
  height: 100%;
  background: radial-gradient(134.34% 134.34% at 50% 0%, #1F3757 0%, #131537 100%);
}
body {
 margin-top: 30px;
}
.Board {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 254;
    width: 284;
    margin-top: 50px;
}


.paper-circle {
    display: flex;
    justify-content: center;
    position: absolute;
    left: -180px;
    top: -100px;
    height: 200px;
    width: 200px;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border-radius: 50%;
    border: 40px solid #4664F4;
   
}

.icon {
    align-self: center;
    height: 74px;
    width: 74px;
}

.scissor-circle {
    display: flex;
    justify-content: center;
    position: absolute;
    right: -180px;
    top: -100px;
    height: 200px;
    width: 200px;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border-radius: 50%;
    border: 40px solid #EB9F0E;
}

.rock-circle {
    display: flex;
    justify-content: center;
    position: absolute;
    left: 50%;
    bottom: -200px;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    height: 200px;
    width: 200px;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border-radius: 50%;
    border: 40px solid #DB2E4D;
    
}

.rock-circle:hover,.paper-circle:hover,.scissor-circle:hover{
    box-shadow:0 0 0 30px hsl(0, 0%, 30%, 30%);
}


.Header {
    height: 150px;
    width: 700px;
    border: 3px solid rgba(255, 255, 255, 0.289199);
    border-radius: 15px;
    font-family: 'Barlow Semi Condensed', sans-serif;
    
    display: flex;
    align-items: center;
    justify-content: space-between;

    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    padding-left: 20px;
    padding-right: 20px;
}

.score {
    height: 115px;
    width: 150px;
    background: linear-gradient(0deg, #F3F3F3 0%, #FFFFFF 100%);
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    border-radius: 8px;
    text-align: center;
}

.score-title {
    color: #2A45C2;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.5px;
}

.score-number {
    color: #565468;
    font-size: 64px;
}
.choices {
    display: flex;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: 'Barlow Semi Condensed', sans-serif;
    font-size: 24px;
    line-height: 32px;
    /* identical to box height, or 133% */
    letter-spacing: 3px;
    color: #FFFFFF;
    text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    text-align: center;
}

.choices-circle {
    display: flex;
    align-content: center;
    justify-content: center;
    height: 300px;
    width: 290px;
    background: linear-gradient(0deg, #F3F3F3 0%, #DADADA 98.34%);
    border-radius: 50%;
    border: 20px solid #DB2E4D;
}

.message {
    margin-top: 200px;

    -webkit-animation: center;

            animation: center;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

@-webkit-keyframes center {
    0% {
       opacity: 0;
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
 
}

@keyframes center {
    0% {
       opacity: 0;
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
 
}

.game-message {
    font-size: 56px;
    line-height: 67px;
    text-align: center;
    color: #FFFFFF;
    text-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
}

button {
    background: linear-gradient(0deg, #F3F3F3 0%, #FFFFFF 100%);
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.196706);
    border-radius: 8px;
    height: 48px;
    width: 220px;
}

@-webkit-keyframes left {
    0% {
        -webkit-transform: translateX(-0%);
                transform: translateX(-0%);
    }
    100% {
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
}

@keyframes left {
    0% {
        -webkit-transform: translateX(-0%);
                transform: translateX(-0%);
    }
    100% {
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
}

@-webkit-keyframes right {
    0% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    100% {
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
}

@keyframes right {
    0% {
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    100% {
        -webkit-transform: translateX(50%);
                transform: translateX(50%);
    }
}

.my-choice {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-animation: left;
            animation: left;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

.computer-choice {
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    -webkit-animation: right;
            animation: right;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
}

.choices-circle.Paper {
    border: 45px solid #4664F4;
}

.choices-circle.Scissors {
    border: 45px solid #EB9F0E;
}

.choices-circle.Rock {
    border: 45px solid #DB2E4D;
}

.choices-circle .icon {
    height: 110px;
    width: 110px;
}

.WON {
    transition: 1s;
    box-shadow: 0 0 0 80px hsl(0, 0%, 30%, 30%), 0 0 0 160px hsl(0, 0%, 30%, 20%), 0 0 0 240px hsl(0, 0%, 30%, 10%);
}
